@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.wrapper {
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
    margin-right: 8%;
    margin-left: 8%;
}

.title {
    float: left;
  }
  
.score, .high-score {
    float: right;
}

.title, .score, .high-score {
    font-weight: bolder;
    font-size: 42px;
}

.score {
    padding-right: 20px;
}

.header {
    padding: 0px 10px 0px 10px;
    border-bottom: 3px inset rgb(224, 224, 224);
    margin-bottom: 20px;
    height: 4rem;
    width: 100%;
    color: black;
    text-shadow: 1px 1px rgb(117, 117, 117);
    background: white;
    position: fixed;
    z-index: 100;
}



img {
    width: 220px;
    height: 220px;
}

img:hover {
    border: 4px blue;
    border-style: outset;
    border-radius: 5px;
    opacity: 0.92;
}

.card {
    position: relative;
}

.card > .imgs {
    float: right;
}

.card > .imgs:first-child {
    float: left;
}
* {
  box-sizing: border-box;
}

html, #root {
  height: 100%;
  font-family: 'Source Code Pro', monospace;
}

body {
  background-image: url("https://i.pinimg.com/originals/dc/8d/ef/dc8def609c27f9123c4f61a83a3b93bd.jpg");
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
}

